import { regex_dict } from '@catoms/Form/form_utils';
import moment from 'moment';
import * as y from 'yup';

y.addMethod(y.string, "assert", function (f: (v) => boolean, errorMessage) {
	return this.test(`assert`, errorMessage, function (value) {
		const { path, createError } = this;
		return f(value) || createError({ path, message: errorMessage });
	});
});

const schema = y.object({
	primary: y.object({
		name: y
			.string()
			.required()
			.matches(/^[A-Za-z ]*$/, "Only words"),
		// middle: y
		// 	.string()
		// 	.optional()
		// 	.matches(/^[A-Za-z ]*$/, "Only words"),
		// last: y
		// 	.string()
		// 	.required()
		// 	.matches(/^[A-Za-z ]*$/, "Only words"),
		dob: y.string()["assert"]((s) => moment().diff(s, "years") >= 18, "Primary must be 18+"),
		apply: y.boolean().default(true),
		sex: y.string().required(),
		ssn: y.string().matches(regex_dict.ssn, "Not an SSN"),
		phone: y.string().matches(regex_dict.phone, "Not a phone"),
		// tobacco: y.boolean().default(false),
		email: y.string().email(),
		address: y.object({
			line1: y.string().required(),
			zip: y.string().required(),
			city: y.string().required(),
			state: y.string().required(),
		}),
		migratory_status: y.string().required(),
		recidence_number: y.string().required(),
		annual_income: y.number().min(1000).required(),
		// phones: y
		// 	.array(
		// 		y.object({
		// 			number: y.string().matches(regex_dict.phone, "Not a phone"),
		// 			type: y.mixed().oneOf(["cell", "home", "office"]).default("cell"),
		// 		})
		// 	)
		// 	.min(1),
	}),
});

export default schema;